<template>
  <div class="forget-conatainer">
      <head-top head-title="重置密码" goBack="true"></head-top>
        <form class="restForm"  action="" v-on:submit.prevent>
            <section class="input-container phone-number">
                <input type="text" placeholder="账号" name="phone" maxlength="11" v-model="phoneNumber" @input="inputPhone">
            </section>
            <section class="input-container ">
                <input type="text" placeholder="旧密码" name="phone" v-model="oldPassword" >
            </section>
            <section class="input-container ">
                <input type="text" placeholder="请输入新密码" name="phone" v-model="newPassword" >
            </section>
            <section class="input-container ">
                <input type="text" placeholder="请确认密码" name="phone"  v-model="confirmPassword" >
            </section>
            <section class="input-container captcha_code_container">
                <input type="text" placeholder="验证码" name="phone" maxlength="6" v-model="moobileCode" >
                <div class="img_chage_img">
                    <img :src="captchaCodeImg" v-show="captchaCodeImg" alt="">
                    <div class="chang_img" @click="getcaptchaCode">
                        <p>看不清楚</p>
                        <p>换一张</p>
                    </div>
                </div>
            </section>
        </form>
        <div class="login_container" @click="resetButton">确认修改</div>
      <alert-tip v-if="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-tip>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
import {mobileCode, checkExsis, sendMobile, getcaptchas, changePassword} from '../../service/getData'
import alertTip from '../../components/common/alertTip'
export default {
    data () {
        return {
            phoneNumber:null,//电话号码
            oldPassword:null,
            newPassword:null,//新密码
            rightphoneNumber:false,
            confirmPassword:null,//确认密码
            captchaCodeImg:null,//验证码地址
            moobileCode:null,//短信验证码
            computedTime:0,//倒计时
            showAlert:false,
            alertText:null,
            accountType:'mobile',//注册方式
            captchaCodeImg:null,
        }
    },components: {
        headTop,
        alertTip
    },created () {
        this.getcaptchaCode();
    },methods: {
       //获取图形验证码
       async getcaptchaCode()
       {
           let res=await getcaptchas();
           this.captchaCodeImg=res.code;
       },
       inputPhone()
       {

       },
       async resetButton()
       {
            if (!this.phoneNumber) {
                    this.showAlert = true;
                    this.alertText = '请输入正确的账号';
                    return
                }else if(!this.oldPassword){
                    this.showAlert = true;
                    this.alertText = '请输入旧密码';
                    return
                }else if(!this.newPassword){
                    this.showAlert = true;
                    this.alertText = '请输入新密码';
                    return
                }else if(!this.confirmPassword){
                    this.showAlert = true;
                    this.alertText = '请输确认密码';
                    return
                }else if(this.newPassword !== this.confirmPassword){
                    this.showAlert = true;
                    this.alertText = '两次输入的密码不一致';
                    return
                }else if(!this.moobileCode){
                    this.showAlert = true;
                    this.alertText = '请输验证码';
                    return
                }
            let res=await changePassword(this.phoneNumber,this.oldPassword,this.newPassword,this.confirmPassword,this.moobileCode);
            if(res.message)
            {
                this.alertText=res.message;
                this.showAlert=true;
                this.getcaptchaCode();
            }else{
                this.alertText="修改成功";
                this.showAlert=true;
            }

       }
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.restForm{
    margin-top: 2.2rem;
    background: #fff;
    .input-container{
        padding:0.4rem;
        display: flex;
        input{
            padding:0.2rem;
            @include sc(0.7rem,#333);
        }
        border-bottom:0.025rem solid #ddd;
    }
    .captcha_code_container{
        height: 2.2rem;
       .img_chage_img{
           display: flex;
           align-items: center;
          // justify-content: flex-end;
          margin-left:1.2rem;
           img{
               @include wh(3.5rem,1.5rem);
           }
           .chang_img{
               display: flex;
               flex-direction: column;
               flex-wrap: wrap;
               justify-content: center;
            p{
               @include sc(0.5rem,#333);
            }
            p:nth-of-type(2)
            {
                color:$blue;;
                padding-top: 0.2rem;
            }
           }
       }
    }
}
.login_container{
    @include wh(90%,2rem);
    background: #4cd964;
    margin-top: 0.8rem;
    border-radius: 0.2rem;
    text-align: center;
    @include sc(0.7rem,#fff);
    line-height: 2rem;
    margin-left: 0.7rem;
}
</style>



